.icon.small {
    height: 36px;
}

.icon.medium {
    width: 48px;
}

.icon.large {
    width: 60px;
}

@media (max-width: 1200px) {
    .icon.small {
        height: 24px;
    }

    .icon.medium {
        width: 2rem;
    }

    .icon.large {
        /* min-width: 24px; */
        max-width: 3rem;
        object-fit: scale-down;
    }
}

@media (max-width: 700px) {
    .icon.small {
        height: 40px;
    }

    .icon.medium {
        width: 54px;
    }
    
    .icon.large {
        max-width: 60px;
    }
}